<template>
    <div class="side" v-show="showSider">
        <div class="side-btn">
            <a href="/usercenter.html#/"><img src="../../src/assets/img/index-nav-icon-my.png" alt="">
                <div class="hove-box" :style="{backgroundColor:red}">个人中心</div>
            </a>
            <a ><img style="z-index: 5" src="../../src/assets/img/index-nav-icon-shop.png" alt="">
                <div v-if="!cart_notice" class="hove-box" :style="{backgroundColor:red}"> <a href="/usercenter.html#/cart">购物车<img style="z-index: 6" src="../../src/assets/img/index-nav-icon-shop.png" alt=""></a></div>
                <transition name="fade">
                    <div v-if="cart_notice" class="notice-box">
                        <div class="success clearfix">
                            <img class="fl" src="../assets/img/details_icon_succes.png" alt="">
                            <span class="fl">添加购物车成功</span>
                        </div>
                        <div class="recommend al">
                            <p style="color: black;
                         ">其他购物达人还加购了</p>
                            <ul>
                                <li class="cursor" @click="toDetail(i.product_id)" v-for="(i,k) in recommendList" :key="k">
                                    <img width="80" height="80" :src="i.default_img|img" :alt="i.product_name">
                                    <p>{{i.product_name}}</p>
                                    <p class="price bold">{{i.jifen/storage.getStorage('exchange').exchange}}{{storage.getStorage('exchange').unit}}</p>
                                </li>
                            </ul>
                        </div>
                        <div class="close-btn cursor" @click="cart_notice = !cart_notice">
                            <img src="../assets/img/details_icon_close.png" alt="">
                        </div>
                        <div class="triangle"></div>
                    </div>
                </transition>
            </a>
            <a><img style="z-index: 5" src="../../src/assets/img/index-nav-icon-collect.png" alt="">
                <div v-if="!collect_notice" class="hove-box" :style="{backgroundColor:red}"><a href="/usercenter.html#/cont">我的收藏<img src="../../src/assets/img/index-nav-icon-collect.png" alt=""></a></div>
                <transition name="fade">
                    <div v-if="collect_notice" class="notice-box">
                        <div class="success clearfix">
                            <img class="fl" src="../assets/img/details_icon_succes.png" alt="">
                            <span class="fl">添加收藏夹成功</span>
                        </div>
                        <div class="recommend al">
                            <p style="color: black;
                         ">其他购物达人还收藏了</p>
                            <ul>
                                <li class="cursor" @click="toDetail(i.product_id)" v-for="(i,k) in recommendList" :key="k">
                                    <img width="80" height="80" :src="i.default_img|img" :alt="i.product_name">
                                    <p>{{i.product_name}}</p>
                                    <p class="price bold">{{i.jifen/storage.getStorage('exchange').exchange}}{{storage.getStorage('exchange').unit}}</p>
                                </li>
                            </ul>
                        </div>
                        <div class="close-btn cursor" @click="cart_notice = !cart_notice">
                            <img src="../assets/img/details_icon_close.png" alt="">
                        </div>
                        <div class="triangle"></div>
                    </div>
                </transition>
            </a>
            <!--<a href="/usercenter.html#/message"><img src="../../src/assets/img/index-nav-icon-news.png" alt="">-->
                <!--<div class="hove-box" :style="{backgroundColor:red}">消息中心</div>-->
            <!--</a>-->
            <a>
                <img src="../../src/assets/img/index-nav-icon-news.png" alt="">
                <div style="width: 140px" class="hove-box" :style="{backgroundColor:red}">{{business_phone}}</div>
            </a>
        </div>
        <div class="side-bottom">
            <a href="/feedback.html">
                <img src="../../src/assets/img/index-nav-icon-aubmit.png" alt="">
                <div class="hove-box" :style="{backgroundColor:red}">意见反馈</div>
            </a>
            <a @click="scrollToPosition(0)">
                <img src="../../src/assets/img/index-nav-icon-top.png" alt="">
            </a>
        </div>
    </div>
</template>

<script>
    export default {
        name: "sidebar",
        data() {
            return {
                showSider: true,
                cart_notice:false,
                collect_notice:false,
                recommendList:[],
                business_phone:this.storage.getStorage('business_data').business_phone ||'4008-703-700',
            }
        },
        filters:{
          img(item) {
              let testUrl = /http:\/\//i;
              if (testUrl.test(item)) {
                  return item
              } else {
                  return `http://admin.fufentong.com/media/upload${item}`
              }
          }
        },
        mounted() {
            window.addEventListener('resize', () => {
                if (window.sider) {
                    clearTimeout(sider)
                }
                window.sider = setTimeout(() => {
                    if (window.innerWidth > 1100) {
                        this.showSider = true
                    } else {
                        this.showSider = false
                    }
                }, 100)
            });
            this.recommend();
            this.bus.$on('cart_notice',function () {
                this.cartNotice()
            }.bind(this));
            this.bus.$on('collect_notice',function () {
                this.collectNotice()
            }.bind(this));
        },
        methods:{
            scrollToPosition(){
                document.querySelector('html').scrollIntoView({behavior: "smooth", block: "start", inline: "start"});
            },
            recommend(){
                this.yum('api/ai/dailyRecommendation').then(res=>{
                    if (res.data.status === 200){
                        this.recommendList = res.data.data.sort(this.randomsort).slice(0,4);
                    }
                })
            },
            randomsort(a, b) {
                return Math.random()>.5 ? -1 : 1;
                //用Math.random()函数生成0~1之间的随机数与0.5比较，返回-1或1
            },
            toDetail(id){
                window.open(`/goods.html#/${id}`)
            },
            cartNotice(){
                this.cart_notice = true;
                setTimeout(()=>{
                    this.cart_notice= false;
                    this.bus.$emit('topNav')
                },3000)
            },
            collectNotice(){
                this.collect_notice = true;
                setTimeout(()=>{
                    this.collect_notice= false;
                    this.bus.$emit('topNav')
                },3000)
            }
        },
        computed:{
            red(){
                if (this.storage.getStorage('business_data')){
                    return this.storage.getStorage('business_data').bgcolor
                }else{
                    return "#ff0077"
                }
            }
        }
    }
</script>

<style scoped lang="stylus">
    .side
        width 35px
        height 100%
        min-height 700px
        background-color #262626
        position fixed
        right 0
        top 0
        z-index 5
        &-btn
            position relative
            text-align center
            margin-top 300px
            a
                display block
                height 30px
                line-height 30px
                position relative
                color white
                margin-bottom 10px
                img
                    position absolute
                    right 7px
                    top 5px
                    z-index 7
                .hove-box
                    display none
                    position absolute
                    z-index 6
                    text-align left
                    width 105px
                    right 0
                    top 0
                    padding-left 10px
                    a
                        margin-bottom 0
                .notice-box
                    position absolute
                    right 45px
                    box-shadow 0 0 8px #ccc
                    width 400px
                    height 270px
                    background-color white
                    padding 20px
                    cursor default
                    img
                        position relative
                        right 0
                        top 0
                    .triangle
                        position: absolute;
                        top 20px
                        right: -5px;
                        width: 10px;
                        height: 10px;
                        background-color: white;
                        transform: rotate(45deg);
                        z-index: 0;
                    .close-btn
                        width 15px
                        height 15px
                        position absolute
                        right 10px
                        top 10px
                        img
                            position absolute
                            left 1px
                            top 1px
                    .success
                        position relative
                        padding-bottom 20px
                        border-bottom 1px solid #ccc
                        color black
                        span
                            font-size 16px
                            color black
                            font-weight bold
                            margin-left 20px
                            line-height 36px
                    .recommend
                        position relative
                        li
                            display inline-block
                            width 80px
                            height 135px
                            margin-right 10px
                            img
                                position relative
                            p
                                font-size 12px
                                line-height 12px
                                height 24px
                                color black
                                text-align center
                                margin-top 10px
                                overflow hidden
                                white-space nowrap
                                text-overflow ellipsis
                                &.price
                                    height 12px
                                    color #ff0077
                &:hover .hove-box
                    display block
        &-bottom
            position absolute
            width 100%
            bottom 16px
            a
                display block
                height 30px
                line-height 30px
                position relative
                color white
                text-align center
                .hove-box
                    display none
                    position absolute
                    z-index 6
                    text-align left
                    width 105px
                    right 0
                    top 0
                    padding-left 10px
                &:hover .hove-box
                    display block
</style>