<template>
    <div class="nav-bar">
        <div class="container main-search clearfix">
            <div class="logo fl">
                <div v-if="storage.hasItem('business_data')" class="nav-log" title="福分通，专业的企业福利解决方案。" :style="{backgroundImage:`url(${logosync})`}"></div>
                <div v-else class="nav-log" title="福分通，专业的企业福利解决方案。" :style="{backgroundImage:`url(${logoDefault})`}"></div>
            </div>
            <div class="search fl">
                <div class="search-input">
                    <div class="search-class">
                        <input type="text" :style="{borderColor:red}" v-model="keyword" @keyup.enter="search()">
                        <button :style="{backgroundColor:red}" @click="search()">搜索</button>
                    </div>
                    <!--<ul>-->
                        <!--<li><a :href="`/grade.html?limit=20&order=asc&type=3&page=1&keyword=牛排&token`">牛排</a></li>-->
                        <!--<li><a :href="`/grade.html?limit=20&order=asc&type=3&page=1&keyword=月饼&token`">月饼</a></li>-->
                        <!--<li><a :href="`/grade.html?limit=20&order=asc&type=3&page=1&keyword=中秋&token`">中秋</a></li>-->
                        <!--<li><a :href="`/grade.html?limit=20&order=asc&type=3&page=1&keyword=海鲜&token`">海鲜</a></li>-->
                        <!--<li><a :href="`/grade.html?limit=20&order=asc&type=3&page=1&keyword=礼包&token`">礼包</a></li>-->
                    <!--</ul>-->
                </div>
            </div>
        </div>
        <div class="full-width main-nav" :style="{borderColor:red}">
            <div class="container">
                <div class="tile" :style="{backgroundColor:red}">全部商品分类</div>
                <div class="nav">
                    <ul>
                        <li><a :class="{active:active==='index'}" href="/index.html">商城首页</a></li>
                        <li><a :class="{active:active==='grade'}" href="/grade.html?limit=20&order=asc&type=3&page=1">商品中心</a></li>
                        <!--<li><a href="http://www.fufentong.com/Home/Active/before.html">活动专区</a></li>-->
                        <li><a href="/grade.html?limit=20&order=asc&type=3&page=1&can=1">我能购买</a></li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'navgator',
        props: {
            active: String
        },
        data() {
            return {
                keyword:'',
                // logo:this.storage.getStorage('business_data').shoplogo1 || '',
                business_data:this.storage.getStorage('business_data'),
                logoDefault:'./logo-combination.png'
            }
        },
        created() {
            if (this.$route) {
                this.keyword = this.$route.query.keyword
            }
        },
        computed:{
          red(){
              if (this.storage.getStorage('business_data')){
                  return this.storage.getStorage('business_data').bgcolor
              }else{
                  return "#ff0077"
              }
          },
            logosync(){
              if (this.storage.getStorage('business_data')){
                  let $url = this.storage.getStorage('business_data').shoplogo1;
                  if(/^http/.test($url)){
                      return $url.replace('com/n0', 'com/img');
                  }else if(/^\/.{4}\//.test($url)){
                      // /2017/12/25/5a40b39bc9819.jpg
                      return 'http://master.fufentong.com/media/upload'+$url;
                  }else{
                      // /tmp/uploads/20181127/4410c3daa57707828e7e242aa97395fa.png
                      return 'http://mapi.fufentong.com'+$url;
                  }
              }else{
                  return './logo-combination.png'
              }
            }
        },
        methods: {
            search() {
                location.href = `/grade.html?limit=20&order=asc&type=3&page=1&keyword=${this.keyword}&token`
            }
        }
    }
</script>

<style scoped lang="stylus">
    .nav-bar
        .main-search
            .logo
                height 150px
                line-height 150px
                display inline-block
                margin-right 120px
                .nav-log
                    display inline-block
                    margin-top 35px
                    width 200px
                    height 80px
                    background-position 0 0
            .search
                height 150px
                display inline-block
                padding-top 50px
                &-class
                    width 760px
                    height 40px
                    line-height 40px
                    vertical-align bottom
                    position relative
                    input
                        position absolute
                        bottom 0
                        display inline-block
                        height 40px
                        width 625px
                        outline none
                        border 2px solid
                        padding-left 10px
                    button
                        position absolute
                        right 0
                        bottom 0
                        display inline-block
                        height 40px
                        line-height 40px
                        width 135px
                        color #fff
                        border-style none
                        font-size 17px
                        font-weight bold
                        outline none
                        cursor pointer
                &-input
                    ul
                        height 20px
                        margin-top 10px
                        li
                            padding 0 15px
                            height 14px
                            line-height 14px
                            display inline-block
                            border-right 1px solid #888
                            color #888
                            font-size 11px
                            a
                                line-height 14px
                                height 14px
                                vertical-align text-top
                            &:last-child
                                border-right none
        .main-nav
            height 40px
            line-height 40px
            border-bottom 2px solid
            .tile
                display inline-block
                width 180px
                font-size 16px
                font-weight bold
                color #fff
                padding-left 20px
            .nav
                display inline-block
                ul
                    display inline-block
                    li
                        display inline-block
                        margin-left 30px
                        a
                            color #000
                            font-size 16px
                            font-weight bold
                            display block
                            &.active
                                color #ff0077
</style>