import axios from 'axios';
import qs from 'qs';
import promise from 'es6-promise'
promise.polyfill();
import storage from '../components/storage'

export default function (_url, _data, methods) {
    let _token = null;
    let site = 'master';
    let dev = window.location.hostname.split('.')[0];
    if (storage.hasItem(`FFT`)) {
        _token = storage.getStorage(`FFT`).token;
    }
    if(storage.hasItem('business_data')){
        site = storage.getStorage('business_data').business_code
    }
    let url;
    url = `http://mapi.fufentong.com/${site}/`;
// if (window.location.protocol === 'http:') {
//     url = `http://mapi.fufentong.com/${site}/`
// } else {
//     url = `https://mapi.fufentong.com/${site}/`
// }
    if (dev === 'localhost' || dev === '192' || dev === 'test') {
        url = `http://mapi.fufentong.com/${site}/`
    }
    let content = {token:_token};
    URL = url + _url;
    if (_data) {
        content = Object.assign(content,_data);
    }
    content = qs.stringify(content);
    if (methods === 'get') {
        return axios.get(URL, {params:content})
    } else {
        return axios.post(URL, content)
    }
}
