<template>
    <div class="ieNotice" v-if="show">
        <div class="container clearfix">
            <span>推荐使用Chrome浏览器获得更好的体验</span>
            <a style="margin-left: 30px;color: #0077ff" href="https://www.google.cn/chrome/" target="_blank">点击下载</a>
            <a class="fr" @click="show = false">关闭</a>
        </div>
    </div>
</template>

<script>
    import isIE from './isIE'
    export default {
        name:'ieNotice',
        data() {
            return {
                show:false
            }
        },
        created(){
            if (isIE() != -1){
                this.show = true
            }
        }
    }
</script>

