<template>
    <div class="top-bar">
        <ieNotice></ieNotice>
        <div class="mask"></div>
        <div class="container clearfix">
            <div class="fl ">
                <div v-if="inter!='商城首页'"><a href="/index.html">{{inter}}</a></div>
                <div class="wxh">微信公众号
                    <img width="100" src="../../src/assets/img/qc.png" alt="">
                </div>
            </div>
            <div class="fr">
                <ul class="top-list">
                    <li class="child ac">
                        <a v-if="inter === '商城首页'" href="/index.html" class="bold">
                            <img class="home" width="16" src="../assets/home.png" alt="">
                            <img class="home hove" width="16" src="../assets/home_pink.png" alt="">
                            {{inter}}</a>
                    </li>
                    <li class="child ac">
                        <a href="/user/card-login.html">卡券兑换</a>
                    </li>
                    <li class="child ac">
                        <a v-if="userinfo.username" href="/usercenter.html">{{userinfo.username?userinfo.username:'请登录'}}
                            <img v-if="userinfo.username" src="../../src/assets/img/down.png" alt="">
                        </a>
                        <a v-else href="/user/clogin.html">{{userinfo.username?userinfo.username:'请登录'}}

                        </a>
                        <ul class="menu-list" v-if="userinfo.username">
                            <li><a href="/usercenter.html">个人中心</a></li>
                            <li><a @click="ClearLogin">退出登录</a></li>
                        </ul>
                    </li>
                    <li class="child ac">
                        <a href="/usercenter.html#/order?tab=1">我的订单
                            <img src="../../src/assets/img/down.png" alt="">
                        </a>
                        <ul class="menu-list">
                            <li><a href="/usercenter.html#/order?tab=2">待支付({{nums.orderNums.noPayNum}})</a></li>
                            <li><a href="/usercenter.html#/order?tab=3">待发货({{nums.orderNums.noSendNum}})</a></li>
                            <li><a href="/usercenter.html#/order?tab=4">待收货({{nums.orderNums.noReceiveNum}})</a></li>
                        </ul>
                    </li>
                    <li class="child ac"><a href="/usercenter.html#/cart">购物车({{nums.cartNums}})</a></li>
                    <li class="child ac"><a href="/usercenter.html#/cont">收藏夹({{nums.collectNums}})</a></li>
                    <li class="child ac">
                        <a href="/help.html">帮助中心
                            <img src="../../src/assets/img/down.png" alt="">
                        </a>
                        <ul class="menu-list">
                            <li><a href="/help.html#/25">联系客服</a></li>
                            <li><a href="/help.html">常见问题</a></li>
                            <li><a href="/feedback.html">意见反馈</a></li>
                        </ul>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
    import ieNotice from './ieNotice'
    export default {
        name: 'top',
        props: {
            inter: {
                default: '欢迎来到福分通商城',
                type: String
            }
        },
        data() {
            return {
                red: '#ff0077',
                showMask:false,
                nums: {
                    collectNums:0,
                    cartNums:0,
                    orderNums:{
                        noPayNum:0,
                        noReceiveNum:0,
                        noSendNum:0
                    }
                },
                userinfo:{}
            }
        },
        created(){
            this.LoginStatus()
            this.topNav()
        },
        mounted(){
          this.bus.$on('topNav',function () {
              this.topNav()
          }.bind(this))
        },
        methods: {
            topNav() {
                this.yum('api/index/topNav').then(res => {
                    this.nums = res.data.data;
                    this.storage.addStorage('fft-order-nums',this.nums);
                })
            },
            LoginStatus(){
                if (this.storage.hasItem('FFT')){
                    this.yum('api/member/getMemberInfo').then(res=>{
                        if (res.data.status === 200) {
                            this.storage.addStorage('fft-user-info',res.data.data);
                            this.userinfo = res.data.data;
                            let ex = {};
                            ex.exchange = parseInt(res.data.exchange);
                            ex.unit = res.data.unit;
                            this.storage.addStorage('exchange',ex);
                        } else {
                            if  (location.pathname === '/index.html'||location.pathname === '/'){
                                return
                            }else{
                                location.assign('/user/clogin.html')
                            }
                        }
                    })
                }else{
                    if  (location.pathname === '/'||location.pathname === '/index.html'){
                        return
                    }
                    location.assign('/user/clogin.html')
                }
            },
            ClearLogin(){
                this.storage.removeStorage('FFT');
                this.storage.removeStorage('business_data');
                this.storage.removeStorage('exchange');
                this.storage.removeStorage('fft-order-nums');
                this.storage.removeStorage('fft-user-info');
                location.assign('/user/clogin.html')
            }
        },
        components:{
            ieNotice
        }
    }
</script>

<style scoped lang="stylus">
    img.home{
        transform translateY(-2px)
    }
    img.home.hove{
        display none
    }
    .top-bar {
        min-height: 30px;
        line-height: 30px;
        background-color: #f5f5f5;
        .fl div {
            display: inline-block;
            color: #333;
            font-size: 12px;
            margin-right: 20px;
            img {
                display: none;
                position: absolute;
                z-index: 2;
            }
        }
        .fl div.wxh:hover {
            img {
                display: inline-block;
                transform translateX(-20px)
            }
        }
        .fr .top-list {
            display: block;
            li {
                position relative
                display: inline-block;
                min-width 70px;
                color: #333;
                font-size: 12px;
                box-sizing content-box
                border 1px solid rgba(0, 0, 0, 0)
                a {
                    padding 0 10px
                    display block
                }
                ul {
                    position absolute
                    display none
                    width 100%
                    z-index 5
                    background-color white
                    border 1px solid #ccc
                    border-top none
                    box-sizing content-box
                    left -1px
                    top 28px
                    li {
                        position relative
                        width 100%
                        &:hover {
                            border none
                        }

                    }
                }
            }
            li.chil:hover {
                border-bottom none
                box-sizing content-box
                img {
                    transform rotate(180deg)
                }
                img.home{
                    transform translateY(-2px)
                    display none
                }
                img.home.hove{
                    transform translateY(-2px)
                    display inline-block
                }
            }
            li.child:hover {
                background-color white
                border 1px solid #ccc
                border-bottom none
                box-sizing content-box
                img {
                    transform rotate(180deg)
                }
                img.home{
                    transform translateY(-2px)
                    display none
                }
                img.home.hove{
                    transform translateY(-2px)
                    display inline-block
                }
                ul {
                    display block
                    li {
                        border none
                    }
                }
            }
        }
    }
</style>